    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;
  }
}

.price__header {
  text-align: center;
}
.price__label {
  font-size: 1rem;
  font-weight: 400;
  color: orange;
  font-family: "Poppins", sans-serif;
}
@media screen and (min-width: 768px) {
  .price__label {
    font-size: 1.125rem;
  }
}

.price__title {
  font-size: 1.75rem;
  font-weight: 700;
  text-align: center;
  color: green;
  margin-top: 0.5rem;
}
@media screen and (min-width: 768px) {
  .price__title {
    font-size: 2.25rem;
    margin-top: 0.75rem;
  }
}

.price__description {
  font-size: 0.875rem;
  line-height: 1.8;
  text-align: center;
  color: #111;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .price__description {
    font-size: 1rem;
    margin-top: 3.125rem;
  }
}

.price__table-wrapper {
  margin-top: 2.5rem;
  overflow-x: auto;
}
@media screen and (min-width: 768px) {
  .price__table-wrapper {
    margin-top: 3.125rem;
  }
}

.price__table {
  width: 100%;
  border-collapse: collapse;
  background-color: #fff;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.1);
}

.price__table-header {
  background-color: #e8f0f5;
  color: #111;
  font-size: 0.875rem;
  font-weight: 700;
  padding: 1rem 0.75rem;
  text-align: center;
  border-bottom: 1px solid #d0d0d0;
}
@media screen and (min-width: 768px) {
  .price__table-header {
    font-size: 1rem;
    padding: 1.25rem 1rem;
  }
}

.price__table-data {
  color: #111;
  font-size: 0.875rem;
  padding: 1rem 0.75rem;
  text-align: center;
  border-bottom: 1px solid #e0e0e0;
}
@media screen and (min-width: 768px) {
  .price__table-data {
    font-size: 1rem;
    padding: 1.25rem 1rem;
  }
}

.price__table tbody tr:last-child .price__table-data {
  border-bottom: none;
}

.price__courses {
  list-style: none;
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .price__courses {
    margin-top: 3.125rem;
  }
}

.price__course {
  padding: 1.5rem;
  background-color: #fff;
  border-radius: 0.75rem;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 768px) {
  .price__course {
    padding: 2rem;
  }
}

.price__course-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: green;
  padding-left: 1.875rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  .price__course-title {
    font-size: 1.5rem;
  }
}

.price__course-title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  background-image: url(../images/common/music-note.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
@media screen and (min-width: 768px) {
  .price__course-title::before {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.price__course-description {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .price__course-description {
    margin-top: 1.25rem;
  }
}

.price__course-description-text {
  font-size: 0.875rem;
  line-height: 1.8;
  color: #111;
  margin-top: 0.75rem;
}
@media screen and (min-width: 768px) {
  .price__course-description-text {
    font-size: 0.9375rem;
    margin-top: 1rem;
  }
}

.price__course-description-text:first-child {
  margin-top: 0;
}

.price__course-list {
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .price__course-list {
    margin-top: 1.75rem;
  }
}

.price__course-item {
  display: flex;
  gap: 1rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid #f0f0f0;
}
@media screen and (min-width: 768px) {
  .price__course-item {
    gap: 1.5rem;
    padding: 1rem 0;
  }
}

.price__course-item:last-child {
  border-bottom: none;
}

.price__course-term {
  flex-shrink: 0;
  width: 5rem;
  font-size: 0.875rem;
  font-weight: 700;
  color: #111;
}
@media screen and (min-width: 768px) {
  .price__course-term {
    width: 6.25rem;
    font-size: 1rem;
  }
}

.price__course-desc {
  flex: 1;
  font-size: 0.875rem;
  color: #111;
}
@media screen and (min-width: 768px) {
  .price__course-desc {
    font-size: 1rem;
  }
}

.price__notes {
  list-style: none;
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .price__notes {
    margin-top: 1.75rem;
  }
}

.price__note {
  font-size: 0.8125rem;
  color: #666;
  line-height: 1.8;
  margin-top: 0.5rem;
}
@media screen and (min-width: 768px) {
  .price__note {
    font-size: 0.875rem;
  }
}

.price__note:first-child {
  margin-top: 0;
}

.trial {
  padding-top: 6.25rem;
  padding-bottom: 3.75rem;
  background-color: #fefcf8;
}
@media screen and (min-width: 768px) {
  .trial {
    padding-top: 10rem;
    padding-bottom: 5rem;
  }
}

.trial__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .trial__content {
    flex-direction: row;
    justify-content: center;
    gap: 3.75rem;
  }
}

.trial__text-wrapper {
  text-align: center;
}
@media screen and (min-width: 768px) {
  .trial__text-wrapper {
    text-align: left;
  }
}

.trial__price {
  font-size: 1.75rem;
  font-weight: 700;
  color: orange;
  font-family: "Poppins", sans-serif;
}
@media screen and (min-width: 768px) {
  .trial__price {
    font-size: 2.25rem;
  }
}

.trial__text {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111;
  line-height: 1.6;
  margin-top: 0.75rem;
}
@media screen and (min-width: 768px) {
  .trial__text {
    font-size: 2rem;
    margin-top: 1rem;
  }
}

.trial__message {
  font-size: 1rem;
  color: #111;
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .trial__message {
    font-size: 1.125rem;
    margin-top: 1.5rem;
  }
}

.trial__button-wrapper {
  margin-top: 2rem;
}
@media screen and (min-width: 768px) {
  .trial__button-wrapper {
    margin-top: 2.5rem;
  }
}

.trial__button {
  display: inline-block;
  padding: 1rem 3rem;
  font-size: 1rem;
  font-weight: 700;
  color: #111;
  background-color: #f4e4d4;
  border-radius: 1.875rem;
  text-decoration: none;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  .trial__button {
    padding: 1.125rem 3.75rem;
    font-size: 1.125rem;
    border-radius: 2.1875rem;
  }
}

.trial__button:hover {
  opacity: 0.8;
}

.trial__image {
  width: 15.625rem;
  height: 15.625rem;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
}
@media screen and (min-width: 768px) {
  .trial__image {
    width: 18.75rem;
    height: 18.75rem;
  }
}
.trial__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}